<template>
  <div>
    <el-row style="margin-bottom:20px;">
      <el-card shadow="always">
        <span class="content-title">数据字典</span>
        <el-button
          type="success"
          @click="openAddDialog()"
        >新增</el-button>
        <el-button
          type="text"
          style="float: right; padding: 3px 0"
          @click="moreSelect"
        ><i :class="selectClass"></i>更多选项</el-button>
        <div
          class="advanced-box"
          v-if="content_show"
        >
          <el-form
            :inline="true"
            :model="forms"
            class="demo-form-inline"
          >
            <el-form-item label="类型">
              <el-select
                v-model="forms.dictionary_type"
                placeholder="请选择"
                style="width:100%"
              >
                <el-option
                  v-for="item in dictionary_list"
                  :key="item.value"
                  :label="item.name"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item>
              <el-button
                type="primary"
                @click="onSubmit"
              >查询</el-button>
            </el-form-item>
          </el-form>
        </div>
      </el-card>
    </el-row>
    <el-table
      border
      :data="tableData"
    >
      <el-table-column
        prop="index"
        label="序号"
        align="center"
        width="60"
      >
      </el-table-column>
      <el-table-column
        prop="show_name"
        label="所属类型"
        align="center"
        width="280"
      >
      </el-table-column>
      <el-table-column
        prop="name"
        label="名称"
        align="center"
      >
      </el-table-column>
      <el-table-column
        prop="value"
        label="字典值"
        align="center"
      >
      </el-table-column>
      <el-table-column
        label="操作"
        align="center"
        width="250"
      >
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            @click="handleEdit(scope.$index, scope.row)"
          >编辑</el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)"
          >删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
      title="新增"
      :visible.sync="dialogVisible"
      width="40%"
      top="5vh"
      :append-to-body='true'
      :before-close="handleClose"
      :closeOnClickModal='false'
    >
      <div class="el-dialog-news">
        <el-form
          label-position="top"
          :rules="rules"
          ref="formLabelAlign"
          label-width="80px"
          :model="formLabelAlign"
        >
          <el-form-item label="类型">
            <el-select
              v-model="formLabelAlign.code"
              placeholder="请选择"
              style="width:100%"
              :disabled="formLabelAlign.id?true:false"
            >
              <el-option
                v-for="item in dictionary_list"
                :key="item.value"
                :label="item.name"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item
            label="字典项名称"
            prop="name"
          >
            <el-input v-model="formLabelAlign.name"></el-input>
          </el-form-item>
          <el-form-item 
            label="字典项值"
            prop="value"
          >
            <el-input v-model="formLabelAlign.value"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="handleClose">取 消</el-button>
        <el-button
          type="primary"
          @click="addInfo('formLabelAlign')"
        >确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      selectClass: "el-icon-arrow-down",
      content_show: false,
      forms: {
        dictionary_type:'banner_module'
      },
      //表格
      tableData: [],
      currentPage: 1,
      page_size: 15,
      total: 0,
      //弹出框
      dialogVisible: false,
      //表单
      formLabelAlign: {
        code: "banner_module",
        name: "",
        value: "",
        sort_no: "1",
      },
      rules: {
        name: [{ required: true, message: "请输入字典项名称", trigger: "blur" }],
        value: [{ required: true, message: "请输入字典项值", trigger: "blur" }],
      },
      foot_type_list: [],
      dictionary_list:[
        {name:'页面类型',value:'banner_module'},
        {name:'新闻类型',value:'news_type'},
        {name:'产品类型',value:'product_type'},
        {name:'解决方案类型',value:'solution_type'},
        {name:'案例类型',value:'case_type'},
        {name:'组件类型',value:'component_type'},
        {name:'foot类型',value:'foot_type'},
        {name:'应用场景',value:'use_scene'},
        {name:'应用产品',value:'use_product'},
        {name:'应用地区',value:'use_area'},
      ]
    };
  },
  mounted() {
    this.getInfoList(this.forms.dictionary_type);
  },
  methods: {
    handleClose() {
      var that = this;
      this.$confirm('是否保存？',{
        showClose:false
      })
      .then(() => {
        that.addInfo('formLabelAlign');
      })
      .catch(() => {this.dialogVisible = false});
    },
    openAddDialog() {
      this.formLabelAlign = {
        code: this.forms.dictionary_type,
        name: "",
        value: "",
        sort_no: "1",
      };
      this.dialogVisible = true;
    },
    moreSelect() {
      if (this.selectClass == "el-icon-arrow-down") {
        this.selectClass = "el-icon-arrow-up";
        this.content_show = true;
      } else {
        this.selectClass = "el-icon-arrow-down";
        this.content_show = false;
      }
    },
    //查询
    onSubmit() {
      this.getInfoList(this.forms.dictionary_type);
    },
    //编辑
    handleEdit(index, row) {
      this.dialogVisible = true;
      this.formLabelAlign = {
        id: row.id,
        code: row.code,
        name: row.name,
        value: row.value,
        sort_no: row.sort_no,
      };
    },
    //删除
    handleDelete(index, row) {
      var that = this;
      this.$confirm("是否确认删除该条信息?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          var params = { id: row.id };
          that.submitInfo("core/dictionary/delete", params, "删除成功!");
        })
        .catch(() => {});
    },
    //编辑框提交按钮
    addInfo(formType) {
      var that = this;
      this.$refs[formType].validate(valid => {
        if (valid) {
          var method = "";
          var info = "";
          if (that.formLabelAlign.hasOwnProperty("id")) {
            method = "core/dictionary/update";
            info = "修改成功!";
          } else {
            method = "core/dictionary/create";
            info = "添加成功!";
          }
          that.submitInfo(method, that.formLabelAlign, info);
        } else {
          return false;
        }
      });
    },
    getInfoList(type) {
      var that = this;
      var params = { code: type};
      axios.get("core/dictionary/list", { params }).then(
        function(response) {
          var code = response.code;
          if (0 == code) {
            var show_name = "";
            for(var i = 0;i < that.dictionary_list.length;i++){
              if(type == that.dictionary_list[i].value){
                show_name = that.dictionary_list[i].name;
                break;
              }
            }
            var json = response.data;
            for(var i = 0;i < json.length;i++){
              json[i].index = i + 1;
              json[i].show_name = show_name;
            }
            that.tableData = json;
          } else {
          }
        },
        function(error) {}
      );
    },
    submitInfo(method, params, info) {
      var that = this;
      axios.post(method, params).then(
        function(response) {
          var code = response.code;
          if (0 == code) {
            that.$message({
              type: "success",
              message: info
            });
            that.dialogVisible = false;
            that.getInfoList(that.forms.dictionary_type);
          } else {
            that.$message({
              type: "success",
              message: "操作失败:" + response.message
            });
          }
        },
        function(error) {}
      );
    },
  }
};
</script>

<style scoped>
.content-title {
  margin-right: 20px;
}
.advanced-box {
  padding: 20px 10px;
  margin-top: 15px;
  background-color: #f7f7f7;
}
.pager {
  float: right;
  padding: 10px 0;
}
.el-dialog__header {
  border-bottom: 1px solid #ccc;
}
.el-dialog-news {
  height: 330px !important;
  padding-right: 15px;
  overflow-y: scroll;
}
.el-dialog__footer {
  border-top: 1px solid #ccc;
}
.el-form-item__label {
  padding-bottom: 0 !important;
}
</style>


